.info {
    display: table-row;
    margin-bottom: 8px;
}

.info .title {
    margin-right: 10px;
    padding-right: 10px;
    display: table-cell;
}

.info .titleText{
    font-size: 12px;
    text-transform: uppercase;
    color: #90949c;
}

.info .content {
    padding-right: 5px;
    display: table-cell;
}

.info .contentText{
    font-size: 14px;
    color: #545454;
    text-transform: uppercase;
}


