.coverRoot{
    /* margin-top:-55px;*/
    width:100%;
    /*max-width:1084px; */
    background-color:#666666;
    overflow: hidden;
}
.coverWrap{
    margin-bottom:-4px;
}
.coverImageDiv{
    max-height:400px;
}
.coverImage{

}
.followBtn{
    /* background-color: #ffffff; */
}

.coverPlatte{
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    height: 108px;
    margin-top: -108px;
    padding: 24px;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    background-color: rgba(0,0,0,0);
    background-image: -webkit-gradient(linear,left top, left bottom,from(rgba(0,0,0,0)),to(rgba(0,0,0,.46)));
    background-image: -webkit-linear-gradient(rgba(0,0,0,0),rgba(0,0,0,.46));
    background-image: -o-linear-gradient(rgba(0,0,0,0),rgba(0,0,0,.46));
    background-image: linear-gradient(rgba(0,0,0,0),rgba(0,0,0,.46));

    display: -ms-flexbox;

    display: flex;
    text-align:left;
}


.coverPlatte .platteName{
    text-align: left;
    margin-left: 24px;
    -ms-flex: 1 1;
        flex: 1 1;
    margin-top: -30px;
}

.coverPlatte .platteName a{
    color: #fff;
    text-decoration: none;
}

.coverPlatte .primary{
    color: rgba(0,0,0,0.87);
    display: block;
    font-size: 25px;
    font-weight: 500;
    color:#ffffff;
}

.coverPlatte .secondary{
    color: rgba(0,0,0,0.87);
    display: block;
    font-size: 14px;
    font-weight: 500;
    color:#ffffff;
}

.profileTab{
    background-color:#666666;
}

@media (max-width:600px) {
    .coverRoot{
        /*margin-top:-25px; */
    }
}

@media screen and (max-width: 672px){
    .coverPlatte .primary{
        font-size: 18px;
    }
    .coverPlatte .secondary{
        font-size: 11px;
    }
}

/* Start Follow Button */
.btn-follow{
    cursor: pointer;
}
.btn-follow{
	height:30px;
	font-size:13px;

	-webkit-box-shadow:0px 0px 0px;

	        box-shadow:0px 0px 0px;
	color: #4b4f56;
	text-align: center;
	line-height: 10px;
	text-transform: capitalize;
	border-radius: 5px;
    outline: 0 none !important;
    overflow: hidden;
    padding: 9px 14px 8px;
    position: relative;
	font-weight: 600;
}
.follow-block .btn-follow{
	height:25px;
	font-size:11px;
	padding: 8px 14px 6px;
}
.btn-follow.follow{
	border:1px solid #ddd !important;
	background-color:#f6f7f9;
	background-image: -webkit-gradient(linear, left top, left bottom, from(#fff), to(#f6f7f9));
	background-image: -webkit-linear-gradient(#fff, #f6f7f9);
	background-image: -o-linear-gradient(#fff, #f6f7f9);
	background-image: linear-gradient(#fff, #f6f7f9);
}


.btn-follow.following:hover span{
	display:none;
}
.btn-follow.following:hover:before{
	content:'Unfollow';
}

.btn-follow.following:hover{
	background-color:#e81c4f;
	border:0px !important;
	color:#fff;
	background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0,0,0,0)), to(rgba(0,0,0,0.15)));
	background-image: -webkit-linear-gradient(rgba(0,0,0,0), rgba(0,0,0,0.15));
	background-image: -o-linear-gradient(rgba(0,0,0,0), rgba(0,0,0,0.15));
	background-image: linear-gradient(rgba(0,0,0,0), rgba(0,0,0,0.15));
}


.coverUploader{
    height: 32px;
    margin: 10px 0 0 10px;
    position: absolute;
    width: 32px;
    z-index: 999;
}

.avatarUploader{
    margin: -58px 0 0 0px;
    position: absolute;
    width: 32px;
    z-index: 999;
}
/* End Follow Button */


